// 视图管理接口
import http from '../httpConfig'
const table = 'mrbase_sys_datamodel_table'


// 运行视图管理语句
export const runSql = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/view/runSql/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

