<template>
  <v-card>
    <v-card-text class="pa-5"
                 style="box-sizing: border-box">

      <v-row dense>
        <v-col cols="12"
               sm="6">
          <mrc_ess_input :label="$t('存储过程名称') + '*'"
                         :readonly="true"
                         placeholder='运行sql自动获取存储过程名称'
                         v-model="params.table_name"></mrc_ess_input>
        </v-col>

        <!-- judegNational('table_name') -->
        <v-col cols="12"
               sm="6">
          <mrc_ess_multiText :label="$t('描述')"
                             style="width:100%"
                             v-model="params.table_description"
                             :isNational="judegNational('table_description')"
                             :readonly="disabled"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               sm="12"
               class="d-flex flex-column mb-3"
               style="align-items: flex-end">
          <v-textarea v-model.trim="params.view_sql"
                      style="width: 100%"
                      :readonly='disabled'
                      label="SQL*"></v-textarea>
          <v-btn color="blue-grey lighten-4"
                 v-if="sqlChange"
                 style="width: 100px;text-align: right"
                 @click="Execute">Execute</v-btn>
        </v-col>
        <v-col cols="12"
               sm="12">
          <v-card v-if="sqlResult"
                  style="height:200px;
                overflow-y:auto;
                border: 1px solid #ededed;
                border-radius: 10px; ">
            <v-card-text>
              <p>error log</p>
              {{sqlResult}}
            </v-card-text>
          </v-card>
        </v-col>

      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       v-if="!disabled"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { delProcedure, runSql } from '@/service/api/dataView'
export default {
  components: {},
  props: {
    paramsProps: {
      type: Object,
      default: () => { }
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // disabled: false,
      params: {
        table_name: '',
        table_description: '',
        view_sql: '',
        para_name: '',
        para_desc: ''
      },
      sqlResult: '',
      sqlChange: false
    };
  },
  validations: {
    params: {
      table_name: {
        required
      }
    }
  },
  computed: {},
  created () { },
  watch: {
    paramsProps: {
      handler (val) {
        this.params = val
      },
      deep: true
    },
    'params.view_sql' (val, oldVal) {
      if (oldVal) {
        this.sqlChange = true
      }


    }
  },
  async mounted () {
    await this.national('mrbase_sys_datamodel_procedure')
  },

  methods: {
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
    },
    success () {
      if (this.params.view_sql) {
        if (this.sqlChange) {
          this.$myConfirm({ content: 'sql语句已改变，请重新运行sql' })
            .catch(() => { }).then(() => { })
          return
        }
      } else {
        this.$myConfirm({ content: '请输入sql语句' })
          .then(() => { }).catch(() => { })
        return
      }

      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('success', this.params)
    },
    async Execute () {
      if (this.params.view_sql) {
        const res = await runSql({ view_sql: this.params.view_sql })
        if (res.code == 200) {
          this.params.table_name = res.data.table_name
          this.sqlChange = false
        } else {
          this.sqlResult = res.msg
        }
      } else {
        this.$myConfirm({ content: '请输入sql语句' })
          .then(() => { }).catch(() => { })
      }
    },
  },
};
</script>

